.port{
    display: flex;
    justify-content: center;
    /* width: 900px; */
    margin-top:1rem;
    margin-bottom:1rem;

}

.container-p{
    width: 900px;
    height: auto;
    margin: 0 auto;
}

.head-name{
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    display:flex ;
    backdrop-filter: blur(60px);
    border-radius:20px ;
    color: #fff;
    margin-bottom: 1rem;
    padding: 1.3rem 2rem;
}

.head-name h1{
    margin: 0;
    font-size: 25px;
    font-weight: 500;
    /* text-align: center; */
}


.port-con{
    display:flex ;
    background: #fff;
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    backdrop-filter: blur(60px);
    border-radius:20px ;
    padding: 1rem 0;
    
    /* opacity: 40%; */
}

.profile{
    /* padding: 2.5rem 0; */
    border-top-left-radius:20px ;
    border-bottom-left-radius:20px ;
    display: flex;
    color: #fff;
    flex-direction: column;
    flex: 1;
}

.pro{
    display: flex;
    /* justify-content: center; */
    flex-direction: column;
    /* background: linear-gradient(111deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.15) ); */
   
    height: auto;
    
}

.proImg{
    display: flex;
    justify-content: center;
}
.proImg img{
    display: flex;
    justify-content: center;
    height: auto;
    width: 115px;
    height: 115px;
    /* filter: brightness(0) saturate(100%) invert(7%) sepia(82%) saturate(6193%) hue-rotate(267deg) brightness(91%) contrast(106%); */

}

.detail{
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin: 0 24px;
    border-bottom: 1px solid gray;
}
.detail h3{
    margin-bottom: 5px;
    font-weight : 500;
    font-size: 20px;
    text-align: center;
}


.detail p{
    margin-top: 0;
    color: rgb(158, 158, 158);
    font-size: 15px;
    margin-bottom: 2rem;
}

.sub-pro{
    display: flex;
    justify-content: center;
    clear: both;
    margin-top: 1rem;    
}

.sub-pro .topic{
    font-size: 15px;
    font-weight: 500;
    text-align: left;
    display: block;
}

.sub-pro .value{
    font-size: 15px;
    text-align: right;
    font-weight: 500;
    display: block;
    margin-left: 136px;
    /* border: 1px solid red; */
    

}


.profile-details{
    flex: 2;
}

.tech{
    display: flex;
    justify-content: center;
    clear: both;
    /* margin-top: 1rem; */
    /* border-top: 0.3px solid rgb(208, 200, 200); */
}



.skills p{
    padding: 0;
    margin-top: 13px;
    margin-bottom: 0px;
    font-weight: 500;
    font-size: 15px;
}


.technicals{
    
    float: right;
    margin-left: 120px;
}

.technicals p{
    padding: 0;
    margin: 0;
    margin-top: 13px;
    font-weight: 500;
    font-size: 15px;
    /* border: 1px solid red; */
    text-align: right;
}

.profile-details .descript{
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 50%, rgba(255,255,255,0.06) );
    color: #fff;
    border-radius: 20px ;
    padding: 1.5rem;
    margin: 0 1rem;
    /* margin-top: 1.5rem; */
}

.profile-details .descript p{
    margin: 0;
    font-size: 15px;
}
.ed-ex{
    display: flex;
    clear: both;
}

.education{
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 50%, rgba(255,255,255,0.06) );
    padding: 1.5rem;
    margin: 1rem;
    margin-right:0 ;
    margin-bottom: 0;
    border-radius: 20px;
    width: fit-content;

}

.education h1{
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 1.5rem;
    font-weight: 600;
    color: #fff;

}

.school{
    margin-bottom: 10px;
}

.logo-ed{
    display:flex ;
    justify-content: center;
}

.education p{
    font-size: 14px;
    margin: 0;
    margin-bottom: .5rem;    
    color: rgb(158, 158, 158);
}

.education img{
    width: 80px;
    height: 80px;

}

.experience-p{
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 50%, rgba(255,255,255,0.06) );
    padding: 1.5rem;
    margin: 1rem;
    margin-right:1rem ;
    margin-bottom: 0;
    border-radius: 20px;
    width: fit-content;

}

.experience-p h1{
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 1.5rem;
    font-weight: 600;
    color: #fff;

}

.experience-p p{
    font-size: 14px;
    margin: 0;
    margin-bottom: .3rem;    
    color: rgb(158, 158, 158);;
}

.project-name{
    margin-bottom: 1.5rem;
}

.project-name a{
    font-size: 12px;
    color: #EBDE01;
    /* top: -10px; */
}

.circle3{
    display: flex;
    position: absolute;
    background:linear-gradient(to right bottom, #0a567b 30%, #29179f 100%);
    filter: blur(50px);
    opacity: 40%;
    height: 300px;
    width: 600px;
    border-radius: 50%;
    z-index: -1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-attachment: fixed;
}


@media screen and (max-width: 480px) {
    .port-con{
        padding: 10px;
        border-radius: 10px;

    }

    .container-p{
        width: 415px;

    }

    .head-name{
        padding: 13px;
        border-radius: 10px;
    }

    .head-name h1{
        font-size: 15px;
    }

    .profile{
        padding: 0;
        margin-right: 10px;
    }

    .pro img{
        width: 2.5rem;
        height: 2.5rem;
    }
    .detail{
        margin: 0 3px;
    }   
    .detail h3{
        font-size: 12px;
    }

    .detail p{
        font-size: 10px;
        margin-bottom: 15px;
    }

    .sub-pro .topic p{
        font-size: 10px;
        margin: 0;
    }

    .sub-pro .value{
        margin-left: 41px;
    }

    .sub-pro .value p{
        font-size: 10px;
        margin: 0;

    }
    .tech{
        margin-top: 10px;
    }

    .tech .skills p{
       font-size: 10px;
       margin-bottom: 5px;
       margin-top: 0;

    }
    .tech .technicals p{
       font-size: 10px;
       margin-top: 0;
       margin-bottom: 5px;

    }

    .tech .technicals{
        margin-left: 30px;
    }

    .profile-details .descript{
        /* width: 225px; */
        padding: 10px;
        margin-right: 0px;
        margin-left: 0px;
        border-radius: 10px;
    }

    .profile-details .descript p{
        margin: 0;
        font-size: 10px;
        
    }

    .ed-ex .education{
        padding: 6px;       
        margin-left: 10px;
        margin: 10px 0;
        margin-bottom: 0;
        border-radius: 10px;
    }

    .school{
        margin-bottom: 10px;
    }

    .ed-ex .education h1{
        font-size: 12px;
        margin-bottom: 10px;
    }

    .ed-ex .education p{
        font-size: 8px;
        margin-bottom: 5px;
    }

    .ed-ex .education img{
        height: 30px;
        width: 30px;
        margin-bottom: 0px;
    }

    .ed-ex .experience-p{
        margin: 10px;
        padding: 6px;
        margin-right: 0;
        margin-bottom: 0;
        border-radius: 10px;
    }

    .ed-ex .experience-p h1{
        font-size: 12px;
        margin-bottom: 10px;
    }

    .ed-ex .experience-p p{
        font-size: 8px;
        margin-bottom: 3px;
    }

    .project-name{
        margin-bottom: 14px;

    }

    .project-name a{
        font-size: 6px;
        margin-top: 0;
    }

    .circle3{
        height: 150px;
        width: 300px;
    }

}

