.contact{
    margin: 0;
    padding: 0;
    /* max-width: 900px; */
    display: flex;
    justify-content: center;
    margin: 1rem 0;

}

.container-con{
    display: flex;
    /* justify-content; */
    flex-direction: column;
    width: 900px;
    /* height: 100vh; */
}

.contact-con{
    display: flex;
    justify-content: center;
    /* flex-direction: ; */
}
