.contact-con .col-md-4{
    display: flex; 
    justify-content: space-between;
    /* flex-direction: column; */
    align-items: center;
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    backdrop-filter: blur(60px);
    border-radius:20px ;
    padding: 1rem ;


}

.col-md-4 .con-link{
    display: flex; 
    justify-content: center; 
    text-align: center;
    align-items: center;
    width: fit-content;
    flex-direction: column;
    width: 235px;
    height: 300px;
    padding: 28px;

 } 

.con-link p{
    color: rgb(158, 158, 158);
    font-weight: 500;
    margin-bottom: 10px;
    margin-top: 20px;
}
.con-link a{
    color: rgb(158, 158, 158);
    font-weight: normal;
}

.con-link a:hover{
    color: #EBDE01;
    font-weight: 700;
}
.contact-con .con-link svg{
    width: 100px;
    height: 100px;
    color: #FFF; 

}

.con-link .e-mail svg{
    font-size: 100px;
}

@media screen and (max-width : 480px) {
    .head-name{
        max-width: 415px;
    }

    .contact{
        height: fit-content;
    }

    .container-con{
        max-width: 415px;
        height: auto;
    }

    .contact-con .col-md-4{
        border-radius: 10px;
        width: 100%;
    }

    .col-md-4 .con-link{
        max-width: 118px;
        max-height: 150px;
        padding: 0;

    }

    .col-md-4 .con-link svg{
        min-width: 50px;
        min-height: 50px;
    }

    .col-md-4 .con-link p{
        font-size:11px ;
        margin-bottom: 10px;
        
    }

    .col-md-4 .con-link a{
        font-size:10px ;
        
    }

    .col-md-4 .con-link a:hover{
        font-size:10px ;
        font-weight: 700px;
    }
}