.banner{
    display: flex;
    justify-content: center;
    margin: 0 auto;

}

.container{
    /* display: flex;
    justify-content: center; */
    margin: 0 auto;
    height: auto;
    

}

.text-popUp.active{
    display: flex;
    position: absolute;
    background: linear-gradient(to bottom, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.0) );
    color: #fff;
    backdrop-filter: blur(10px);
    font-size: 10px;
    top: 5px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 20px 1rem;
    margin: auto 0 ;
    left: 0;
    right: 0;
    transition: 0.3s ease;
    top: 0;
    z-index: 11;
    

}

.text-popUp h1{
    display: none;
    margin: 0 auto;
    top: -100%;
    transition: 0.3s ease;
    /* font-weight: 500; */
}
.text-popUp.active h1{
    display: block;
    margin: 0 auto;
    transition: 0.3s ease;
    font-weight: 500;
}

.text-popUp{
    top: -100%;
    transition: 0.3s ease;
}


.circle1,.circle2{
    display: flex;
    position: absolute;
    /* background-color: red; */
    background:linear-gradient(to right bottom, #061680 30%, #973fcf 100%);
    /* backdrop-filter: blur(10px); */
    filter: blur(60px);
    opacity: 40%;
    height: 500px;
    width: 400px;
    border-radius: 50%;
    /* border: 1px solif red; */
    z-index: -1;
}

.circle1{
    top: -50px;
    left: 10px;
}

.circle2{
    top: 630px;
    right: -50px;
    position: absolute;
    display: none;
}

.banner-con{
    display: flex;
    justify-content: center;
    color: #fff;
    background: #FFF;
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    /* opacity: 90%; */
    margin-top: 5rem;

    width: 900px;
    /* background-filter: blur(10px); */
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    z-index: 2;
    border-radius: 20px;
    /* box-shadow: 3px 3px 3px rgb(22, 4, 43); */
    padding: 2.5rem 0;
    /* height:75vh; */
}

.banner-text{
    height: auto;
    align-items: center;
    text-align: center;
}

.banner-text h1{
    font-size: 96px;
    font-weight: bold;
    margin-top: 0;
}

.banner-text p{
    font-size: 40px;
    /* font-weight: bold; */

}

.banner-text p.sub{
    font-size: 24px;
    /* font-weight: bold; */
}

.input{
  display: flex;
  justify-content: center;
  margin-bottom: .5rem;

}

.input input{
    color: black;
    width: 100%;
    border-radius: 20px;
    padding: 10px;
    text-align: center;
    opacity: 50%;
    backdrop-filter: blur(10px);
    font-family: "Ubuntu";
    font-size: 15px;
    border: 1px solid #EBDE01;

}
input:focus{
    opacity: 100%;
}

.input button{
    color: black;
    background: #EBDE01;
    padding: 10px 40px;
    border-radius: 20px;
    margin-left: 1rem;
    font-family: "Ubuntu";
    font-size: 1rem;
    border: 1px solid #EBDE01;
}

button:hover{
    background: none;
    border:1px solid  #fff;
    color: #FFF;
    opacity: 90%;
    cursor: pointer;
}

@media screen and (max-width : 480px) {
    .banner-con{
        width: 415px;
        border-radius: 10px;
        margin-top: 50px;
    }

    .banner-text{
        width: auto;
    }

    .banner-text h1{
        font-size: 50px;
    }

    .banner-text p{
        font-size: 25px;

    }

    .banner-text p.sub{
        font-size: 15px;
    }

    .input{
        margin-bottom: 0;

    }

    .input input{
        padding: 5px;
        font-size: 12px;
    }

    .input button{
        font-size: 12px;
        padding: 5px 17px;
    }

    .text-popUp.active h1{
        font-size: 15px;
    }

}