.experience{
    display: flex;
    justify-content: center;
}

.container-ex{
    width: 900px;
    height: auto;
    margin: 0 auto;
}

.ex-con{
    display: flex;
    justify-content: center;
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    backdrop-filter: blur(60px);
    border-radius:20px ;
    padding: 1rem 0;
}

.ex-con .ex-description{
    /* background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) ); */
    display: flex;
    flex-direction: column;
    flex: 1;
}


.line-bot{
    margin-bottom: 2rem;
    padding: 0 1.5rem;
    display: flex;
    flex-direction: column;
}

.line-bot h3{
    margin-top: 0;
    color: #fff;
    font-size: 20px;
}

.line-bot p{
    margin-top: 0;
    color: rgb(158, 158, 158);
    font-size: 14px;
    margin-bottom: 1rem;
}

.line-bot .using-line{
    border-top: 1px solid gray;
   
}

.line-bot .use-line{
    border-top: 1px solid gray;
   
}

.line-bot .using-line p{
    margin-top: 1rem;
    margin-bottom: 0;
    font-size: 13px;
    color: #fff;
    font-weight: 500;

}

.line-bot .use-line p{
    margin-top: 1rem;
    margin-bottom: 0;
    text-align: right;
    margin-left: 18px;
    font-size: 13px;

}

.machine-learning{
    padding: 0 1.5rem;
    display: flex;
    margin-top: 1rem;
    flex-direction: column;
}

.line-bot h3{
    margin-top: 0;
    color: #fff;
    font-size: 20px;
}

.machine-learning h3{
    margin-top: 0;
    color: #fff;
    font-size: 20px;
}

.machine-learning p{
    margin-top: 0;
    color: rgb(158, 158, 158);
    font-size: 14px;
    margin-bottom: 1rem;
}

.machine-learning .using-machine{
    border-top: 1px solid gray;
   
}

.machine-learning .use-machine{
    border-top: 1px solid gray;
   
}

.machine-learning .using-machine p{
    margin-top: 1rem;
    margin-bottom: 0;
    font-size: 13px;
    color: #fff;
    font-weight: 500;

}

.machine-learning .use-machine p{
    margin-top: 1rem;
    margin-bottom: 0;
    text-align: right;
    margin-left: 118px;
    font-size: 13px;

}


.ex-con .ex-demo{
    /* background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) ); */
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    margin-right: 1rem;
    flex: 2;
    
    /* justify-content: center; */
}

.ex-demo .line-demo{
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    border-radius: 20px;
    /* width: 100%; */
    height: 355px;
    flex-direction: column;
}

.line-demo .line-img{
    display: flex;
    justify-content: space-between;
}

.line-demo .line-img img{
    height: 225px;
    width: 118px;
    border-radius: 10px;
    /* margin-right: 10px; */
    
}

.line-img .demo{
    margin-right: 1.5rem;
}

.line-demo .line-img video{
    height: 225px;
    width: 118px;
    border-radius: 10px;
    
}


.line-img p{
    margin-bottom: 0;
    color: #fff;
    /* margin-right: 10px; */
    display: flex;
    justify-content: center;
}


.machine-demo{
    display: flex;
    justify-content: center;
    background: linear-gradient(111deg, rgba(255,255,255,0.07) 0%, rgba(255,255,255,0.1) );
    border-radius: 20px;
    padding: 0 1rem;
    margin-top: 1rem;

}

.machine-demo .machine-img{
    display: flex;
    justify-content: space-between;
    padding: 4.5rem 0;
}

.machine-img img{
    /* width: 100%; */
    display: flex;
    justify-content: center;
    height: 134px;
}

.machine-img p{
    color: #fff;
    margin-bottom: 0;
    display: flex;
    justify-content: center;
}

.machine-img .demo{
    margin-right: 3rem ;
}

@media screen and (max-width : 480px) {
    .head-name{
        max-width: 415px;
       
    }

    .container-ex{
        max-width: 415px;
        flex-direction: column;
        flex: none;
    }

    .ex-con{
        flex-direction: column;
        padding-bottom: 0;
    }

    .ex-description{
        width: 100%;
    }
    .tech{
        margin-top: 0;
        justify-content: space-between;
    }

    .line-bot h3{
        font-size:12px;
    }
    .line-bot p{
        font-size:10px;
    }

    .line-bot .using-line{
        border-top: none;
    }

    .line-bot .use-line{
        border-top: none;
    }

    .line-bot .using-line p{
        font-size: 10px;
        margin-top: 5px;
    }

    .line-bot .use-line p{
        font-size: 10px;
        margin-top: 5px;

    }

    .machine-learning{
        margin-top: 0;
    }
    .machine-learning h3{
        font-size: 12px;
    }

    .machine-learning p{
        font-size: 10px;
        margin: 0;
    }

    .machine-learning .using-machine{
        border-top: none;
        margin-top: 10px;
    }

    .machine-learning .use-machine{
        border-top: none;
        margin-top: 10px;

    }

    .machine-learning .using-machine p{
        font-size: 10px;
        margin-top: 5px;
    }
    .machine-learning .use-machine p{
        font-size: 10px;
        margin-top: 5px;
    }

    .ex-con .ex-demo{
        max-width: 415px;
        padding: 1rem;
        margin-right: 0;
        margin-top: 10px;
    }

    .ex-demo .line-demo{
        max-height: 200px;
        max-width: 350px;
        margin: 0;
        justify-content: none;
        
    }

    .line-img{
        max-width: 415px;
        
    }

    .line-img img{
        max-width: 79px;
        max-height: 132.5px;
    }

    .line-img .demo video{
        width: 79px;
        height: 132.5px;
    }

    .line-img .demo{
        margin-right: 10px;
    }
    .line-img .demo p{
        font-size: 10px;
    }

    .machine-demo{
        display: flex;
        justify-content: center;
    }

    .machine-demo .machine-img{
        padding: 1.5rem 0;
        justify-content: center;
        margin: 0 ;
        /* align-items: center; */
    }

    .machine-img .demo img{
        /* max-width: 158px; */
        max-height: 100px;
        display: flex;
        margin-right: 0;
        
    }

    .machine-img .demo{
        margin-right: 30px;
    }

    .machine-img .demo p{
        font-size: 10px;
    }
}
